<template>
  <v-container fluid class="action">
    <v-container>
      <v-container>
        <v-row>
          <h1>Oppsie, please provide username 🙂 </h1>
        </v-row>
      </v-container>
      <v-container fluid>
        <v-text-field
          flat
          solo
          background-color="#FCE4EC"
          label="Username"
          v-model="userName"
          class="field"/>
      </v-container>
      <v-row style="justify-content: right">
        <v-btn
        depressed
        color="#FCE4EC"
        class="gorabels"
        @click="goToUser"
      >
        Gorabels
      </v-btn>
      </v-row>
    </v-container>
  </v-container>
</template>

<script setup>
import { ref } from 'vue'
import router from '../router/index'

const userName = ref('')

const goToUser = () => {
    if(userName.value == '') {
      alert('Kelangan nga kase ng username')
    } else {
      router.replace(`/${userName.value}`).catch(()=>{});
    }
  }
</script>


