  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin: auto;
  max-width: 500px;
}

.field {
  border-radius: 10px !important;
}

.gorabels {
  margin-left: 24px;
  margin-right: 24px;
  color: "#FCE4EC" !important;
  border-radius: 10px !important;
  text-transform: unset !important;
  font-weight: bold;
}
